*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  display: flex;
  flex-flow: row wrap;
}

.block {
  display: flex;
  height: 300px;
  min-width: 300px;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 100px;
  flex-grow: 1;
  flex-basis: 100%;
}

.block:nth-child(1) {
  background-color: rgb(100%,0%,0%);
}

.block:nth-child(2) {
  background-color: rgb(80%,0%,0%);
}

.block:nth-child(3) {
  background-color: rgb(60%,0%,0%);
}

.block:nth-child(4) {
  background-color: rgb(40%,0%,0%);
}

.block:nth-child(5) {
  background-color: rgb(20%,0%,0%);
}

.block:nth-child(6) {
  background-color: rgb(0,0,0);
}

@media (min-width:600px) and (max-width:899px) {
  .block {
    flex-basis: 50%;
  }
  .block:nth-child(4) {
    order: 1;
  }
  .block:nth-child(3) {
    order: 2;
  }
  .block:nth-child(5) {
    order: 2;
  }
  .block:nth-child(6) {
    order: 2;
  }
}

@media (min-width:900px) {
  .block {
    flex-basis: 33%;
  }
  .block:nth-child(4) {
    order: 3;
  }
  .block:nth-child(5) {
    order: 2;
  }
  .block:nth-child(6) {
    order: 1;
  }
}
